.smaller {
  font-size: 0.85em;
}
.smallest {
  font-size: 0.55em;
}
iframe {
  border: 1px solid #aaa;
}
#cover:after {
  opacity: 0;
}
#cover h2 {
  margin: 25px 0 0;
  text-align: center;
  font-size: 50px;
  display: inline-block;
  padding-top: 30px;
  padding-bottom: 30px;
}
#cover p {
  margin: 40px 0 0;
  text-align: center;
  font-size: 24px;
  opacity: 0.75;
  line-height: 1.4;
}
#cover .author {
  position: absolute;
  bottom: 15px;
  left: 36%;
}
#cover .strike {
  text-decoration: line-through;
}
#theend h2 {
  margin-top: -100px;
}
.cover img {
  width: auto;
  height: auto;
  min-width: 100%;
  min-height: 100%;
}
.img-full {
  text-align: center;
}
.img-full img {
  position: absolute;
  top: 0;
  bottom: 0;
  margin-left: -25%;
}
.end-link {
  bottom: 25%;
  position: absolute;
  width: 100%;
  padding: 0 35px;
  font-size: 24px;
  line-height: 1.8;
}
.bigger {
  font-size: 53px;
}
.logo {
  width: 100px;
  background-color: #fff;
  margin-top: 50px;
}
.logo + .logo {
  margin-left: 10px;
}
.logo.css-m-js {
  padding: 18.6px 10px;
  background-color: #2b2727;
}
.compare-table {
  table-layout: fixed;
}
.compare-table th,
.compare-table td {
  width: 15%;
}
.compare-table th:first-child,
.compare-table td:first-child {
  width: 24%;
}
.compare-table th:last-child,
.compare-table td:last-child {
  width: 17%;
}
.compare-table th {
  padding-bottom: 20px;
}
.logo-img {
  position: absolute;
  top: 30%;
  right: 10%;
}
.logo-img.php {
  top: auto;
  bottom: 7%;
  right: 0;
}
.logo-img.sublime-full {
  position: absolute;
  top: 30%;
  left: 0;
  width: 100%;
  opacity: 0.9;
}
.logo-img.winner {
  top: 10%;
}
.logo-img.winner2 {
  top: 15%;
}
th.condition {
  width: 50%;
}
th.editor,
th.editor2 {
  position: relative;
}
th.editor {
  width: 20%;
}
th.editor2 {
  width: 25%;
}
th .table-logo {
  position: relative;
  top: 10px;
}
td {
  padding: 0 10px;
}
td.green {
  background-color: #cff3af !important;
}
.slide ul>li:lang(ru)::before {
  color: #cd0011;
}
.slide ul.plus>li:lang(ru)::before {
  content: '+';
  color: #50891d;
}
.slide ul.minus>li:lang(ru)::before {
  content: '–';
  color: #ec190c;
}
.shout.faded {
  color: #fff;
  font-size: 4em;
  background-color: rgba(0,0,0,0.7);
  padding-bottom: 25px;
}
.gray {
  color: #808080;
}
.slide pre code.hljs {
  line-height: 1.5;
}
.link {
  position: relative;
  margin-left: 1.8em;
  line-height: 1.3em;
}
.link::before {
  left: -1.8em;
  position: absolute;
  top: 0.1em;
  display: inline-block;
  width: 1.5em;
  height: 1.4em;
  content: '';
  background-repeat: no-repeat;
  background-size: contain;
}
.link-github::before {
  background-image: url("pictures/github-logo.svg");
}
.link-npm::before {
  background-image: url("pictures/npm-logo.svg");
  height: 1em;
  top: 0.5em;
}
.link-github-white::before {
  background-image: url("pictures/github-logo-white.svg");
}
